        setTimeout(function () {
          _this8.showCheckmarkIcon = false;
        }, 2000);
      } else {
        handleError(error, errorMessage);
        this.showErrorIcon = true;
        setTimeout(function () {
          _this8.showErrorIcon = false;
        }, 2000);
      }
    },
    onScopeChange: function onScopeChange(scope) {
      this.$emit('update:scope', scope);
    }
  }
};